import time from "../../common/time.js";
export default {
    props:{
        userInfo:Object
    },
    computed:{
        getRegAge(){
            return time.gettime.sumAge(this.userInfo.regtime)
        },
        getStarSign(){
            return time.gettime.getHoroscope(this.userInfo.birthday)
        }
    },
}
